header{
  width:100%;
  height:100px;
  background:rgba(255,255,255,0.5);
  position:fixed;
  z-index:10;
}

header div.inner{
  width:84%;
  max-width:1800px;
  min-width:1200px;
  /* border:1px solid black; */
  height:100%;
  margin:0 auto;
}

header div.inner h1{
  /* border:1px solid orange; */
  padding-top:20px;
  float:left;
}

header div.inner h1 img{
  /* border:1px solid green; */
  width:60px;
}

header div.inner nav{
   /* border:1px solid gray; */
   float:left;
   padding-top:30px;
   margin-left:5.21vw;
}

header div.inner nav ul{
  /* border:1px solid cyan; */
}

header div.inner nav ul li{
  /* border:1px solid orchid; */
  display:inline-block;
}

header div.inner nav ul li a{
  /* background:red; */
  padding:5px 1.56vw;
  display:block;
}

/* RightMenu */
header div.inner div#RightMenu{
  /* border:1px solid goldenrod; */
  float:right;
  padding-top:20px;
}
header div.inner div#RightMenu ul{
  /* border:1px solid blue; */
  float:left;
}
header div.inner div#RightMenu ul li{
  /* border:1px solid pink; */
  display:inline-block;
}

header div.inner div#RightMenu ul li:nth-child(1) a{
  background:url(../assets/images/login.svg) no-repeat center center;
  background-size:40px;
}

header div.inner div#RightMenu ul li:nth-child(2) a{
  background:url(../assets/images/cart.svg) no-repeat 50% 50%;
  background-size:40px;
}

header div.inner div#RightMenu ul li a{
  /* background:yellow; */
  display:inline-block;
  width:50px;
  height:50px;
}

header div.inner div#RightMenu ul li a span{
  display:none;
}

header div.inner div#RightMenu form{
  /* border:1px solid chocolate; */
  float:left;
  margin-left:10px;
  position:relative;
}

header div.inner div#RightMenu form input[type="text"]{
  border:1px solid #666;
  border-radius: 20px;
  width:250px;
  height:40px;
  padding-left:10px;
}

header div.inner div#RightMenu form input[type="image"]{
  /* border:1px solid chocolate; */
  width:40px;
  vertical-align: bottom;
  margin-left:-6px;
  position:absolute;
  right:5px;
  top:0;
}


/* header 반응형*/
@media all and (min-width:1500px) and (max-width:1699px){
  header div.inner{
    width:90%;
  }
}

@media all and (min-width:1280px) and (max-width:1499px){
  header div.inner{
    width:94%;
  }

  header div.inner nav ul li a{
    padding:5px 1.2vw;
  }

  header div.inner nav{
    margin-left:0vw;
    border:1px solid red;
 }
}

/* Footer Styles */
footer {
  background-color: #333;
  color: #fff;
  padding: 50px 0;
}

.footer-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.footer-info {
  flex: 0 0 60%;
}

.footer-info address {
  font-style: normal;
  margin: 20px 0;
  line-height: 1.6;
}

.footer-social {
  flex: 0 0 30%;
}

.social-links {
  display: flex;
  gap: 20px;
  margin-top: 20px;
}

.social-links a {
  color: #fff;
  font-size: 24px;
  transition: color 0.3s ease;
}

.social-links a:hover {
  color: #ddd;
}

@media (max-width: 768px) {
  .footer-content {
    flex-direction: column;
    gap: 30px;
  }
  
  .footer-info, .footer-social {
    flex: 0 0 100%;
  }
}