#NewProduct {
  position: relative;
  width: 100%;
  height: 100%;
}

/* 신제품 방향버튼 */
.swiper-button-next2,
.swiper-button-prev2 {
  position: absolute;
  top: 50%;
  width: 40px;
  height: 40px;
  margin-top: -20px;
  z-index: 10;
  cursor: pointer;
  background-size: 40px 40px;
  background-position: center;
  background-repeat: no-repeat;
}

.swiper-button-next2:after,
.swiper-button-prev2:after {
  font-family: swiper-icons;
  font-size: 40px;
  text-transform: none !important;
  letter-spacing: 0;
  font-variant: initial;
  line-height: 1;
}

.swiper-button-prev2 {
  left: 10px;
  right: auto;
}

.swiper-button-next2 {
  right: 10px;
  left: auto;
}

.swiper-button-prev2:after {
  content: 'prev';
}

.swiper-button-next2:after {
  content: 'next';
}

/* NewProductSection.css */
.swiper-slide figure {
  overflow: hidden;
