/* NewProductSection.css */
.swiper-slide figure {
  position: relative;
  overflow: hidden;
  cursor: pointer;
}

.swiper-slide figure img {
  transition: transform 0.3s ease;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.swiper-slide figure:hover img {
  transform: scale(1.1);
}

.swiper-slide figure figcaption {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 20px;
  background: rgba(0, 0, 0, 0.7);
  color: white;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.swiper-slide figure:hover figcaption {
  opacity: 1;
}

.swiper-slide figure figcaption h3 {
  font-size: 1.2em;
  margin-bottom: 8px;
}

.swiper-slide figure figcaption p {
  font-size: 0.9em;
  margin: 0;
}



/* ProductSection */
#c02 {
  width: 100%;
  height: 100%;
  background: rgb(135, 211, 143);
}

/* c02 section styles */
/* c02 section styles */
#c02 {
  padding: 50px 0;
}

#c02 .product-container {
  display: flex;
  justify-content: space-between;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

#c02 .product-item {
  border:1px solid red;
  flex: 0 0 31%;
  transition: transform 0.3s ease;
  cursor: pointer;
  overflow: hidden;
}

#c02 .product-item img:hover {
  transform: scale(1.1);
  z-index: 1;
}

#c02 .product-item img {
  width: 100%;
  height: auto;
  border-radius: 8px;
  transition: transform 0.3s ease;
}



